<template>
  <div class="container d-flex flex-column align-items-center py-2">
    <div>
      <img src="../assets/icon/Icon.png" alt="icon" />
    </div>

    <form class="signin-area" @submit.stop.prevent="handleSubmit">
      <div class="mt-3 title">
        <p></p>
      </div>

      <!-- account -->
      <div class="form-label-group mb-2">
        <label for="account"></label>
        <input
          type="text"
          class="form-control mt-3"
          v-model="account"
          name="account"
          id="account"
          required
        />
      </div>

      <!-- name -->
      <div class="form-label-group mb-2">
        <label for="name"></label>
        <input
          type="text"
          class="form-control mt-3"
          v-model="name"
          name="name"
          id="name"
          required
        />
      </div>

      <!-- email -->
      <div class="form-label-group mb-2">
        <label for="email">Email</label>
        <input
          type="text"
          class="form-control mt-3"
          v-model="email"
          name="email"
          id="email"
          pattern="^\w+((-\w)|(\.\w))*@[A-Za-z0-9]+\.[A-Za-z]+$"
          required
        />
      </div>

      <!-- password -->
      <div class="form-label-group mb-2">
        <label for="password"></label>
        <input
          type="password"
          class="form-control mt-3"
          v-model="password"
          name="password"
          id="password"
          autocomplete="off"
          pattern="[0-9]{8}"
          placeholder="8"
          required
        />
      </div>

      <!-- passwordCheck -->
      <div class="form-label-group mb-3">
        <label for="passwordCheck"></label>
        <input
          type="password"
          class="form-control mt-3"
          v-model="passwordCheck"
          name="passwordCheck"
          id="passwordCheck"
          autocomplete="off"
          pattern="[0-9]{8}"
          placeholder=""
          required
        />
      </div>

      <div class="btn-wrapper">
        <button
          class="btn my-2 submit-btn"
          type="submit"
          :disabled="isProcessing"
        >
          {{ isProcessing ? "..." : "" }}
        </button>

        <div>
          <router-link to="/signin" style="font-weight: bold"></router-link>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import { Toast } from "../utils/helpers";
import userAPI from "../apis/users";

export default {
  data() {
    return {
      account: "",
      name: "",
      email: "",
      password: "",
      passwordCheck: "",
      isProcessing: false,
    };
  },

  methods: {
    async handleSubmit() {
      try {
        if (
          !this.account ||
          !this.name ||
          !this.email ||
          !this.password ||
          !this.passwordCheck
        ) {
          return Toast.fire({
            icon: "error",
            title: "",
          });
        }
        if (
          !this.password.match(/[0-9]{8}/) ||
          !this.passwordCheck.match(/[0-9]{8}/) ||
          !this.email.match(/^\w+((-\w)|(\.\w))*@[A-Za-z0-9]+\.[A-Za-z]+$/)
        ) {
          return Toast.fire({
            icon: "error",
            title: "",
          });
        }

        this.isProcessing = true;
        const { data } = await userAPI.create({
          account: this.account,
          name: this.name,
          email: this.email,
          password: this.password,
          passwordCheck: this.passwordCheck,
        });

        if (data.status !== "success") {
          throw new Error(data.message);
        }

        Toast.fire({
          icon: "success",
          title: "",
          closeButtonHtml: true,
        });
        this.$router.push({ name: "sign-in" });
      } catch (error) {
        this.isProcessing = false;
        Toast.fire({
          icon: "error",
          title: "",
        });
      }
    },
  },
};
</script>

<style scoped>
form {
  width: 540px;
}

.submit-btn {
  width: 540px;
  background-color: orangered;
  color: white;
  border-radius: 50px;
}

span {
  font-weight: normal;
}

.form-label-group {
  position: relative;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  background-color: #f5f8fa;
  border-bottom: 2px #657786 solid;
  margin: 20px 0 0 0;
  height: auto;
  text-decoration: none;
}

input {
  border: none;
  background-color: #f5f8fa;
  height: 50%;
  padding-left: 10px;
}
input::-webkit-input-placeholder {
  font-size: 18px;
}

input:focus {
  background-color: #f5f8fa;
  box-shadow: none;
}
input:focus::-webkit-input-placeholder {
  color: transparent;
}

.title {
  font-weight: bolder;
  font-size: 1rem;
}

label {
  position: absolute;
  margin: 5px 0 10px 10px;
  font-size: 15px;
  color: rgb(92, 94, 134);
}
</style>